import React, { Component } from 'react';

const data = [
    {
        mensagem: "Gostei muito desta peça. Ótima máquina, entrega rápida. Em uma semana já estava na minha casa",
        nome: "Antônio Luiz",
        data: "2018"
    },
    {
        mensagem: "Gostei muito do modelo, ótimo atendimento e muita atenção com os clientes. Recomendo a todos.",
        nome: "Carla Rosa",
        data: "2018"
    },
    {
        mensagem: "Eu já tinha o modelo preto, comprei aqui na Impala mesmo. Gosto muito desse modelo, e a qualidade da peça é excelente",
        nome: "André Lorenzo",
        data: "2018"
    }
]

class Avaliacoes extends Component {
    render(){
        return (
            <div className="Avaliacoes">
                <div className="flex vertical">
                    <h2 className="text-center">Avaliações</h2>
                    <br />
                    {
                        data.map((item,idx) => (
                            <div key={idx} className="flex-1 flex vertical flex-center">
                                <p>{item.mensagem}</p>
                                <small>{item.nome} - { item.data }</small>
                                <br />
                                { data.length - 1 !== idx && <hr className="hr-80 hr-gray" />}
                            </div>
                        ))
                    }                   
                </div>
            </div>
        )
    }
}

export default Avaliacoes;